import styled from 'styled-components';

export const Wrapper = styled.div`
& .section-container-search{
    padding-top:10px;
}
& .section-container-collapse{
    background-color: #f4e6ff!important;
}
& .ant-collapse-borderless > .ant-collapse-item {

    border-bottom: none !important;
}
& .ant-card {
    width: 266px!important;
}
& .ant-card-body {
    padding: 24px;
    background: #F4E6FF 0% 0% no-repeat padding-box;
    opacity: 1;
    zoom: 1;
    top: 650px;
    left: 140px;
    width: 266px;
}
& .section-container{
    margin: 0px!important;
    padding-top: 50px!important;
    margin-bottom: 50px!important;
}
& .section-description-card {
    font-family: 'Jost';
    font-size: 15px;
    font-weight: 600;
    letter-spacing: 0px;
    color: #000000;
    opacity: 1;
}
& .tag{
    position: absolute;
    top: 260px;
    right: 3px;
}
& .ant-btn .anticon {
    
    display: flex;
    justify-content: center;
    align-items: center;
}


    /* responsi*/
    @media(max-width: 576px){
        .ant-card {
            width: 100%!important;
        }
        .ant-card-body {
           width: auto;
     }
    }

  /* sm */
  @media (max-width:650){
      .ant-card-body {
          width:560px;
      }
      .ant-card{
          widthe:562px!important;
      }
  }
  ${({ configuration }) => {
    return `
    & .my-buttom{
        background:${configuration && configuration.primary_color || "var(--color-primary)"}!important;
        border-color:${configuration && configuration.primary_color || "var(--color-primary)"}!important;

     }
    `;
}}
`;
