import React, { useEffect, useState } from "react";
import { Col, Layout, Row, Divider, Avatar, Button, Card, Input, Skeleton, message, List, Collapse } from "antd";
import Banner from "../../components/banner/Banner"
import { Wrapper, WrapperList } from "./Styles";
import { getService } from "../../services";
import { translate } from "react-polyglot";
import Loader from "../../components/loader/";
import { useSelector, useDispatch } from "react-redux";
import SearchField from "../../components/search-field/SearchField";


const { Panel } = Collapse;
const FrecuentsQuestions = ({ t, id, record, reference = "faq", filterDefaultValues, ...props }) => {
    const locale = useSelector(({ auth }) => {
        return auth.locale;
    });
    const entity = useSelector(({ auth }) => {
        return auth.entity;
    })
    const [dataSource, setDataSource] = useState([]);
    const [loading, setLoading] = useState(true);
    const [contract, setContract] = useState(false);
    const getData = () => {
        if (reference) {
            const service = getService(reference);
            service.find({
                query: {
                    ...filterDefaultValues
                }
            })
                .then(({ data }) => {
                    setDataSource(data);
                    setLoading(false);
                })
                .catch(err => {
                    message.error(err.message);
                    setLoading(false);
                });
        }
    };
    useEffect(() => {
        getData();
    }, [record]);
    if (loading) return <Loader />;
    return (
        <Wrapper configuration={{
            primary_color: entity && entity.primary_color || "var(--color-primary);",
            secundar_color: entity && entity.secundar_color || "var(--color-secundary)",
            text_color: entity && entity.text_color || "var(--color-white)"
        }}>
            <Banner dataSource={[{
                title: t("faq.bannerTitle"),
                image_path: "/images/PreguntasFrecuentes.jpg",
            }]} />
            <Row className="section section-search" type="flex" justify="center" align="center" gutter={10}>
                <Col className="section-container-search" xl={10} lg={16} md={16} xs={22} >
                    <SearchField
                        placeholder={t("search.Text.Input")}
                        reference="packs"
                        open={false}
                        source={`name_${locale}`}
                    />
                </Col>
            </Row>
            <Row className="section section-container" type="flex" aligntop="center" justify="center" gutter="5">
                <Col span="20">
                    <List
                        grid={{
                            gutter: 10,
                            xs: 1,
                            sm: 2,
                            md: 2,
                            lg: 3,
                            xl: 4,
                            xxl: 4,
                        }}
                        dataSource={dataSource}
                        renderItem={item => (
                            <List.Item>

                                <Card
                                >
                                    <div className="section-description-card" dangerouslySetInnerHTML={{ __html: item[`question_${locale}`] }} />
                                    <Collapse className="section-container-collapse" bordered={false} >
                                        <Panel
                                            showArrow={false}
                                            extra={
                                                <Button
                                                className="my-buttom"
                                                    onClick={() => {
                                                        setContract(show => !show);
                                                    }}
                                                    icon={!contract ? "plus" : "minus"}
                                                    type="primary" shape="circle" />}>
                                            <div className="section-description-card" dangerouslySetInnerHTML={{ __html: item[`response_${locale}`] }} />
                                        </Panel>
                                    </Collapse>

                                    {/*  <Button className="tag" type="primary" shape="circle" icon="plus"></Button>  */}
                                </Card>

                            </List.Item>
                        )}
                    />
                </Col>
            </Row>
        </Wrapper>
    );
}
export default translate()(FrecuentsQuestions);